import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { LoginPage } from './login/login.component';
import { HomePage } from './home/home.page';
import { CreatePage } from './create/create.page';
import { DiscoverPage } from './discover/discover.page';
import { ProfilePage } from './profile/profile.page';
import { AlertPage } from './alert/alert.page';
import { UserSetupPage } from './user-setup/user-setup.page';
import { CategoriesPage } from './categories/categories.page';
import { QuizesPage } from './quizes/quizes.page';
import { authGuard, loginGuard } from './auth.guard';
import { VerifyQuizComponent } from './verify-quiz/verify-quiz.component';
import { UsersComponent } from './users/users.component';
import { LiveQuizesComponent } from './live-quizes/live-quizes.component';
import { OngoingComponent } from './ongoing/ongoing.component';
import { QuizGuardService } from './quiz-guard.service'; // Import the guard
import { HistoryComponent } from './history/history.component';

const routes: Routes = [
  {
    path: 'login',
    component: LoginPage,
  },
  {
    path: 'home',
    component: HomePage,
    canActivate: [authGuard]
  },
  {
    path: 'create',
    component: CreatePage,
    canActivate: [authGuard]
  },
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'discover',
    component: DiscoverPage,
    canActivate: [authGuard]
  },
  {
    path: 'profile',
    component: ProfilePage,
    canActivate: [authGuard]
  },
  {
    path: 'alert',
    component: AlertPage,
    canActivate: [authGuard]
  },
  {
    path: 'setup',
    component: UserSetupPage,
    canActivate: [authGuard]
  },
  {
    path: 'categories',
    component: CategoriesPage,
    canActivate: [authGuard]
  },
  {
    path: 'quizes',
    component: QuizesPage,
    canActivate: [authGuard]
  },
  {
    path: 'live-quizes',
    component: LiveQuizesComponent,
    canActivate: [authGuard]
  },
  {
    path: 'history',
    component: HistoryComponent,
    canActivate: [authGuard]
  },
  {
    path: 'users',
    component: UsersComponent,
    canActivate: [authGuard]
  },
  {
    path: 'verify-quiz',
    component: VerifyQuizComponent,
    canActivate: [authGuard]
  },
  {
    path: 'ongoing',
    component: OngoingComponent,
    canActivate: [authGuard],
    canDeactivate: [QuizGuardService]
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule],
  providers: [QuizGuardService] // Add the guard to providers
})
export class AppRoutingModule { }